html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
ul {
  margin-top: 0;
  margin-bottom: 0;
}

body {
  font-family: 'Roboto', sans-serif;
  color: var(--body-portfolio-text);
  background-color: var(--header-background-color);
}

.search-form {
  display: flex;
  width: 100%;
  background-color: #03668a;
  justify-content: center;
  padding: 10px;
  box-shadow: 0 2px 1px rgba(44, 45, 64, 0.08), 0 1px 1px rgba(44, 45, 64, 0.16),
    0 1px 6px rgba(44, 45, 64, 0.08);
}

input {
  border: none;
  border-radius: 4px;
  margin-right: 10px;
  padding: 10px;
}

input:focus {
  outline: none;
}

.search-form button {
  font-weight: 600;
  background-color: tomato;
  border: none;
  border-radius: 4px;
  padding: 10px;
  color: black;
  cursor: pointer;
  box-shadow: 0 2px 1px rgba(44, 45, 64, 0.08), 0 1px 1px rgba(44, 45, 64, 0.16),
    0 1px 6px rgba(44, 45, 64, 0.08);
}

.container {
  position: relative;
  margin: 20px auto 0;
  padding: 0 20px 60px;
}

.gallery {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
  width: 100%;
}

.photo-card {
  width: 100%;
  height: auto;
  border-radius: 4px;
  flex-basis: calc((100% - 36px) / 4);
  box-shadow: 0 2px 1px rgba(44, 45, 64, 0.08), 0 1px 1px rgba(44, 45, 64, 0.16),
    0 1px 4px rgba(44, 45, 64, 0.08);
}

.info {
  border: 1px solid #dfdfe4;
  border-top: none;
  justify-content: space-between;
  padding: 10px 20px;
  display: flex;
}

.load-more {
  position: absolute;
  width: 120px;
  left: calc(50% - 60px);
  bottom: 10px;
  font-weight: 600;
  background-color: tomato;
  border: none;
  border-radius: 4px;
  padding: 10px;
  color: black;
  cursor: pointer;
  box-shadow: 0 2px 1px rgba(44, 45, 64, 0.08), 0 1px 1px rgba(44, 45, 64, 0.16),
    0 1px 6px rgba(44, 45, 64, 0.08);
}
.hidden {
  display: none;
}
img {
  display: block;
  width: 100%;
  height: 220px;
  object-fit: cover;
}
.info-item {
  flex-direction: column;
  align-items: center;
  display: flex;
}
