html {
  box-sizing: border-box;
}

*, :before, :after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, h5, h6, p, ul {
  margin-top: 0;
  margin-bottom: 0;
}

body {
  color: var(--body-portfolio-text);
  background-color: var(--header-background-color);
  font-family: Roboto, sans-serif;
}

.search-form {
  width: 100%;
  background-color: #03668a;
  justify-content: center;
  padding: 10px;
  display: flex;
  box-shadow: 0 2px 1px rgba(44, 45, 64, .08), 0 1px 1px rgba(44, 45, 64, .16), 0 1px 6px rgba(44, 45, 64, .08);
}

input {
  border: none;
  border-radius: 4px;
  margin-right: 10px;
  padding: 10px;
}

input:focus {
  outline: none;
}

.search-form button {
  color: #000;
  cursor: pointer;
  background-color: tomato;
  border: none;
  border-radius: 4px;
  padding: 10px;
  font-weight: 600;
  box-shadow: 0 2px 1px rgba(44, 45, 64, .08), 0 1px 1px rgba(44, 45, 64, .16), 0 1px 6px rgba(44, 45, 64, .08);
}

.container {
  margin: 20px auto 0;
  padding: 0 20px 60px;
  position: relative;
}

.gallery {
  width: 100%;
  flex-wrap: wrap;
  gap: 12px;
  display: flex;
}

.photo-card {
  width: 100%;
  height: auto;
  border-radius: 4px;
  flex-basis: calc(25% - 9px);
  box-shadow: 0 2px 1px rgba(44, 45, 64, .08), 0 1px 1px rgba(44, 45, 64, .16), 0 1px 4px rgba(44, 45, 64, .08);
}

.info {
  border: 1px solid #dfdfe4;
  border-top: none;
  justify-content: space-between;
  padding: 10px 20px;
  display: flex;
}

.load-more {
  width: 120px;
  color: #000;
  cursor: pointer;
  background-color: tomato;
  border: none;
  border-radius: 4px;
  padding: 10px;
  font-weight: 600;
  position: absolute;
  bottom: 10px;
  left: calc(50% - 60px);
  box-shadow: 0 2px 1px rgba(44, 45, 64, .08), 0 1px 1px rgba(44, 45, 64, .16), 0 1px 6px rgba(44, 45, 64, .08);
}

.hidden {
  display: none;
}

img {
  width: 100%;
  height: 220px;
  object-fit: cover;
  display: block;
}

.info-item {
  flex-direction: column;
  align-items: center;
  display: flex;
}

